<script>
  import { createEventDispatcher } from 'svelte'

	const dispatch = createEventDispatcher();
  export let playlist = [], currentTrack = 0

  let showAddDialog = false
  let newTrack = {title: "The Fundamentals - JS",
                  artist: "Syntax FM",
                  audioSrc: "https://traffic.libsyn.com/syntax/Syntax162.mp3",
                  imgSrc: "https://upload.wikimedia.org/wikipedia/commons/6/6a/JavaScript-logo.png",
                  imgAlt: "Javascript Logo",
                  color1: "#222",
                  color2: "yellow"}

  function addTrack() {
    let escTrack = {}
    Object.keys(newTrack).forEach(key => escTrack[key] = escapeHTML(newTrack[key]))

    dispatch('trackadded', escTrack)
    showAddDialog = false
  }

  function escapeHTML(str) {
    return str.replace(/&/g,'&amp;').replace(/</g,'&lt;').replace(/>/g,'&gt;');
  }
  function selectText() {
    this.select()
  }
</script>

<style>
  .playlist-table { color: white; }

	table {	
    border-collapse: collapse;
    color: white;
  }
	
	tr {
    cursor: pointer;
    transition: all .08s ease-in-out; 
    background: transparent; 
  }
	tr:focus { outline: solid white; }
	tr:not(:first-of-type):hover {
		background: linear-gradient(to right, var(--hi-1), var(--hi-2));
    transform: scale(1.1);
	}
	
	td, th {
		border: solid 1px;
		padding: .2em .8em;
	}

  #add-form {
    position: fixed;
    left: 50vw;
    top: 50vh;
    transform: translate(-50%, -50%);
    padding: 5vmin;
    border-radius: 2vmin;
    color: white;
    background: rgba(25,25,25,0.8);
    z-index: 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  button.close {
    position: absolute;
    top: 3vh;
    right: 3vw;
    width: 4vmin;
    height: 4vmin;
    background: transparent;
    border: none;
    transition: background .11s ease-in-out;
  }
  button.close::before,
  button.close::after {
    content: '';
    position: absolute;
    width: 60%;
    height: .5vmin;
    left: 50%;
    top: 50%;
    --rot-dir: 1;
    transform: translate(-50%, -50%) rotate(calc(var(--rot-dir) * 45deg));
    background: white;
    transition: background .11s ease-in-out;
  }
  button.close::before {
    --rot-dir: -1;
  }
  button.close:hover::before,
  button.close:hover::after {
    background: #444;
  }


  fieldset {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    border: none;
  }

  label {
    display: flex;
    justify-content: flex-end;
    margin: 0;
  }

  input { margin-left: 2vmin;}

  .colors-div {
    display:flex;
    justify-content: stretch;
    width: 100%;
  }

  .colors-div label {
    width: 50%;
    margin: 1vmin;
  }

  button.submit {
    margin: 2vmin;
    padding: 1vmin 2vmin;
    background: transparent;
    color: white;
    border: none;
    transition: background .11s ease-in-out, color .11s ease-in-out;
  }
  
  button.close:hover,
  button.close:focus,
  button.submit:hover,
  button.submit:focus {
    color: #222;
    background: white;
  }
</style>

<div class="playlist-table">
	<h2>Playlist</h2>
	<table>
		<tbody>
			<tr><th>Title</th><th>Artist</th></tr>
			{#each playlist as track, i}
			<tr tabindex=0 on:click={() => dispatch('trackchange', i)} style="--hi-1: {track.color1}; --hi-2: {track.color2};"
          on:keypress={(e) => { if (e.key === " " || e.key === "Enter") dispatch('trackchange', i) }}>
        <td>{track.title}</td><td>{track.artist}</td>
      </tr>
			{/each}
      {#if !showAddDialog}
      <tr tabindex=0 on:click={() => showAddDialog = true}
          on:keypress={(e) => {if (e.key === " " || e.key === "Enter") showAddDialog = true}}>
        <th colspan=2>+ add track</th>
      </tr>
      {/if}
		</tbody>
	</table>
  {#if showAddDialog}
  <div id="add-form">
    <h2>Add New Track</h2>
    <button class="close" on:click={() => showAddDialog = false }></button>
    <fieldset>
      <label>
      Title
      <input type="text" bind:value={newTrack.title} on:click={selectText}>
    </label>
    <label>
      Artist
      <input type="text" bind:value={newTrack.artist} on:click={selectText}>
    </label>
    <label>
      Audio URL
      <input type="text" bind:value={newTrack.audioSrc} on:click={selectText}>
    </label>
    <label>
      Image URL
      <input type="text" bind:value={newTrack.imgSrc} on:click={selectText}>
    </label>
    <label>
      Image Alt Text
      <input type="text" bind:value={newTrack.imgAlt} on:click={selectText}>
    </label>
    <div class="colors-div"style="width: 100%:">
      <label>
        Color 1
        <input type="color" bind:value={newTrack.color1}>
      </label>
      <label>
        Color 2
        <input type="color" bind:value={newTrack.color2}>
      </label>
    </div>
    </fieldset>
    <button class="submit" on:click={addTrack}>Submit</button>
  </div>
  {/if}
</div>