<script>
	import Background from './Background.svelte'
	import AudioPlayer from './AudioPlayer.svelte'
	import PlaylistTable from './PlaylistTable.svelte'
	
	let currentTrack = 0
	let playlist = [
		{
			title: "Rep. Alexandria Ocasia Cortez on Abolishing DHS",
			artist: "WNYC",
			imgSrc: "https://media.wnyc.org/i/800/0/l/85/2019/07/RadioHour-AOC-4x3.jpg",
			imgAlt: "illustration of US Representative Alexandria Ocasia Ortez speaking",
			audioSrc: "https://www.podtrac.com/pts/redirect.mp3/audio.wnyc.org/tnyradiohour/tnyradiohour071219_webhour.mp3",
			color1: "orchid",
			color2: "lightskyblue"
		},
		{
			title: "Technical Writing With Rachel Andrew",
			artist: "Shop Talk Show",
			imgSrc: "https://pbs.twimg.com/profile_images/781380559947915265/wrjtv_jp.jpg",
			imgAlt: "Rachel Andrew in a leather jacket",
			audioSrc: "https://cdn.simplecast.com/audio/167887/167887a0-ac00-4cf9-bc69-b5ca845997db/1c1d3e4f-71dc-48a8-bd12-24a0460a08fd/shoptalkshow_371_tc.mp3",
			color1: "#444",
			color2: "orange"
		},
		{
			title: "Shopify: Tobias Lütke",
			artist: "How I Built This With Guy Raz",
			imgSrc: "https://avatars2.githubusercontent.com/u/347?s=400&v=4",
			imgAlt: "Tobias Lütke",
			audioSrc: "https://play.podtrac.com/npr-510313/ondemand.npr.org/anon.npr-mp3/npr/hibt/2019/08/20190802_hibt_shopify.mp3?orgId=1&d=4035&p=510313&story=747660923&t=podcast&e=747660923&siteplayer=true&size=64414407",
			color1: "rgb(214, 32, 33)",
			color2: "#237bbd"
		}
	]
	let currX = .5, currY = .5
	$: color1 = playlist[currentTrack].color1
	$: color2 = playlist[currentTrack].color2
	
	function mouseMoveParallax(e) {
		currX = (e.pageX / document.body.clientWidth) * 2 - 1
		currY = (e.pageY / document.body.clientHeight) * 2 - 1
	}
	function touchMoveParallax(e) {
		currX = (e.touches[0].pageX / document.body.clientWidth) * 2 - 1
		currY = (e.touches[0].pageY / document.body.clientHeight) * 2 - 1
	}
	function keyDownParallax(e) {
		const speed = .04;
		switch (e.key) {
			case "w":
				currY -= speed
				break
			case "s":
				currY += speed
				break
			case "a":
				currX -= speed
				break
			case "d":
				currX += speed
				break
		}
	}
</script>

<style>
	:global(body) {
		width: 100vw;
		min-height: 100vh;
		margin: 0;
		padding: 0;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-around;
	}
</style>

<svelte:body on:mousemove={mouseMoveParallax} on:touchmove={touchMoveParallax}
						 on:keydown={keyDownParallax}/>
<Background color1={playlist[currentTrack].color1} color2={playlist[currentTrack].color2} />
<AudioPlayer {...playlist[currentTrack]} {currX} {currY}/>
<PlaylistTable playlist={playlist} currentTrack={currentTrack} on:trackchange={(e) => currentTrack = e.detail}
																															 on:trackadded={(e) => {
																															 	console.log(e.detail)
																																playlist = [...playlist, e.detail]
																															 	console.log(playlist)
																															 }}/>